.dzin-editor--wrapper {
  position: absolute;
  right: 0;
  left: 0;
  height: calc(100% - 46px);
}

#root {
  position: relative;
}

.dzin-editor {
  height: calc(100vh - 52px);
  padding: 0px 18px 0px 18px;
  margin-bottom: 48px;
  margin-top: 48px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: -1;
  overflow-y: scroll;
}

.dzin-editor--toolbar {
  position: fixed;
  /*height: 48px;*/
  /*width: 100%;*/
  bottom: 0;
  left: 0;
  right: 0;
  margin-bottom: 0px;
  z-index: 100;
  background: #fff;
  padding: 8px 12px;
  display: flex;
  align-items: center;
  box-shadow: 0 -2px 4px 0 rgba(0, 0, 0, .03), 0 0 2px 0 rgba(0, 0, 0, .04);
}

.toolbar-button {
  border: none;
  background: #fff;
  padding: 4px 8px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 4px;
  margin-right: 8px;
}

.toolbar-button.active {
  background: #F8F8F8;
}

.toolbar-button:hover svg.material-svg,
.toolbar-button:hover svg.vk-svg g > g,
.toolbar-button:hover svg.vk-svg path:last-child {
  fill: #333;
}

.toolbar-button.active svg.material-svg,
.toolbar-button.active svg.vk-svg g > g,
.toolbar-button.active svg.vk-svg path:last-child {
  fill: #F40057;
}

.editor-header {
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 48px;
  background: #fff;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .03), 0 0 2px 0 rgba(0, 0, 0, .04);
  padding: 0px 12px;
  justify-content: flex-end;
}

.button {
  display: inline-block;
  background: transparent;
  border: none;
  color: white;
  padding: 8px 12px;
  border-radius: 8px;
  outline: none;
  background: #F40057;
  font-size: 16px;
  cursor: pointer;
}

.button.button--tertiary {
  color: #F40057;
  background: #F8F8F8;
}

.button.button--tertiary:hover {
  opacity: 0.6;
}